<template>
  <div>
    <section class="entryBg">
      <div class="text-center">
        <img src="../assets/images/reset/entryIcon_3x.png" alt="綠神盾" style="width: 150px">
      </div>
      <div class="d-flex justify-content-center mt-4">
        <div class="divider" />
      </div>
      <div class="d-flex justify-content-center mt-5">
        <div class="entryContent" v-if="expire === 'loading'">
          <h3 class="mb-4">
            Loading...
          </h3>
        </div>
        <div class="entryContent" v-if="expire === 'na'">
          <h3 class="mb-4">
            系統錯誤！請再索取一次設定信，或<a href="mailto:guardtaiwan@gmail.com">Email</a>告訴我們
          </h3>
        </div>
        <div class="entryContent" v-if="expire === 'no'">
          <h3 class="mb-4">
            Hi {{ name }},
          </h3>
          <p>
            要重新設定您的密碼，請點一下以下按鈕
          </p>
        </div>
        <div class="entryContent" v-if="expire === 'yes'">
          <h3 class="mb-4">
            已超過設定時間，請回到APP再索取一次重設密碼信
          </h3>
        </div>
      </div>
      <div class="text-center mt-4 mt-md-5">
        <button class="btnReset" @click="gotoReset" v-if="expire === 'no'">
          設定新密碼
        </button>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data () {
    return {
      name: '',
      expire: 'loading'
    }
  },
  methods: {
    gotoReset () {
      this.$emit('nextPage')
    },
    sendApi () {
      const api = `https://us-central1-shield-bfba7.cloudfunctions.net/resetPwdDone`
      // const api = `https://cors-anywhere.herokuapp.com/us-central1-shield-bfba7.cloudfunctions.net/resetPwdDone`
      const formData = new FormData()
      formData.append('email', this.$route.params.email)
      formData.append('token', this.$route.params.token)
      formData.append('password', '')
      this.$http.post(api, formData).then((response) => {
        if (response.data.data.status === 'success') {
          this.name = response.data.data.name
          this.expire = 'no'
        } else {
          console.log('API fail')
          if (response.data.data.error === 'token_expire') {
            this.expire = 'yes'
          } else {
            this.expire = 'na'
          }
        }
      })
    }
  },
  mounted () {
    this.sendApi()
  }
}
</script>

