<template>
  <div>
    <landingPage class="web" v-show="openDoor" />
    <landingPageMobile class="mobile" v-show="openDoor" />
    <div class="entryBgWeb" v-show="!openDoor">
      <div class="pointer" v-show="!isEnter" id="knock" @click="enter" />
      <div id="open" v-show="isEnter && !openDoor" />
    </div>
    <div class="entryBgMobile" v-show="!openDoor">
      <div class="pointer" v-show="!isEnter" id="knockMobile" @click="enterMobile" />
      <div id="openMobile" v-show="isEnter && !openDoor" />
    </div>
  </div>
</template>

<script>
import landingPage from '@/components/landingPage.vue'
import landingPageMobile from '@/components/landingPageMobile.vue'
export default {
  components: {
    landingPage,
    landingPageMobile
  },
  data () {
    return {
      isEnter: false,
      openDoor: false,
      lottieNow: null, // 用來儲存lottie，方便接下來的調用
      lottieNowMobile: null
    }
  },
  methods: {
    /* eslint-disable */
    enter () {
      const vm = this
      vm.isEnter = true
      vm.lottieNow.play()
      window.setTimeout(() => {
        vm.openDoor = true
      }, 2200)
    },
    enterMobile () {
      const vm = this
      vm.isEnter = true
      vm.lottieNowMobile.play()
      window.setTimeout(() => {
        vm.openDoor = true
      }, 2200)
    }
  },
  mounted () {
    /* eslint-disable */
    var lottieKnock = bodymovin.loadAnimation ({
      container: document.getElementById('knock'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      path: './knock.json'
    })
    var lottieKnock2 = bodymovin.loadAnimation ({
      container: document.getElementById('knockMobile'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      path: './knock.json'
    })
    var lottieOpen = bodymovin.loadAnimation ({
      container: document.getElementById('open'),
      renderer: 'svg',
      loop: false,
      autoplay: false,
      path: './dooropen.json'
    })
    var lottieOpen2 = bodymovin.loadAnimation ({
      container: document.getElementById('openMobile'),
      renderer: 'svg',
      loop: false,
      autoplay: false,
      path: './dooropen.json'
    })
    this.lottieNow = lottieOpen
    this.lottieNowMobile = lottieOpen2
  }
}
</script>

