<template>
  <div>
    <section class="setNewBg">
    <div class="text-center">
      <img src="../assets/images/reset/setNewIcon_3x.png" alt="綠神盾" style="width: 130px">
    </div>
    <div class="d-flex justify-content-center">
      <div class="setNew mt-5">
        <h3 class="text-center">
            設 定 新 密 碼
        </h3>
        <input type="password" placeholder="請輸入您的新密碼" class="form-control mt-4" v-model="newPassword">
        <input type="password" placeholder="請再次輸入您的新密碼" class="form-control mt-4" v-model="confirmPassword">
      </div>
    </div>
    <div class="text-center mt-5">
      <button class="text-white sendBtn" @click="send">
        確認送出
      </button>
    </div>
    </section>
  </div>
</template>

<script>
export default {
  data () {
    return {
      newPassword: null,
      confirmPassword: null
    }
  },
  methods: {
    send () {
      if (this.newPassword !== this.confirmPassword) {
        alert('再確認密碼與新密碼不一致喔')
        this.confirmPassword = null
      } else {
        // const api = `https://us-central1-shield-bfba7.cloudfunctions.net/resetPwdDone`
        const api = `https://cors-anywhere.herokuapp.com/us-central1-shield-bfba7.cloudfunctions.net/resetPwdDone`
        const formData = new FormData()
        formData.append('email', this.$route.params.email)
        formData.append('token', this.$route.params.token)
        formData.append('password', this.newPassword)
        this.$http.post(api, formData).then((response) => {
          if (response.data.data.status === 'success') {
            this.newPassword = null
            this.confirmPassword = null
            alert('已成功修改')
          } else {
            console.log('API fail')
            if (response.data.data.error === 'token_expire') {
              alert('已超過設定時間，請回到APP再索取一次重設密碼信')
            } else {
              alert('修改失敗，請再試一次或Email告訴我們: guardtaiwan@gmail.com')
            }
          }
        })
      }
    }
  }
}
</script>

