<template>
  <div>
    <section class="confirmBg">
      <div class="text-center">
        <img src="../assets/images/confirm/confirmIcon_3x.png" alt="綠神盾" style="width: 150px">
      </div>
      <!-- <div class="d-flex justify-content-center mt-5" v-if="!isSuccess">
        <div class="confirmContent">
          <h3>
            Hi 韓國輸,
          </h3>
          <p class="mt-4">
            歡迎註冊綠神盾,您的註冊流程只差一步就要完成了！請按下方的連結完成認證。
          </p>
