            </button>
          </div>
        </div>
      </div> -->
      <div class="d-flex justify-content-center mt-5">
        <div class="successMessage text-center" v-if="isSuccess === 'loading'">
          <span>
            Loading...
          </span>
        </div>
        <div class="successMessage text-center" v-if="isSuccess === 'na'">
          <span>
            驗證失敗！請再索取一次驗證信，或<a href="mailto:guardtaiwan@gmail.com">Email</a>告訴我們
          </span>
        </div>
        <div class="successMessage text-center" v-if="isSuccess === 'yes'">
          <span>
            驗證成功！請回綠神盾登入
          </span>
        </div>
        <div class="successMessage text-center" v-if="isSuccess === 'no'">
          <span>
            已超過驗證時間，請回到APP再索取一次驗證信
          </span>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isSuccess: 'loading'
    }
  },
  methods: {
    complete () {
      this.isSuccess = 'loading'
    },
    sendApi () {
      const api = `https://us-central1-shield-bfba7.cloudfunctions.net/verifyMailDone`
      // const api = `https://cors-anywhere.herokuapp.com/us-central1-shield-bfba7.cloudfunctions.net/verifyMailDone`
      const formData = new FormData()
      formData.append('email', this.$route.params.email)
      formData.append('token', this.$route.params.token)
      this.$http.post(api, formData).then((response) => {
        if (response.data.data.status === 'success') {
          this.isSuccess = 'yes'
        } else {
          console.log('API fail')
          if (response.data.data.error === 'token_expire') {
            this.isSuccess = 'no'
          } else {
            this.isSuccess = 'na'
          }
        }
      })
    }
  },
  mounted () {
    this.sendApi()
  }
}
</script>

