<script>
import privacy from '@/components/privacy.vue'
import privacyMobile from '@/components/privacyMobile.vue'
export default {
  components: {
    privacy,
    privacyMobile
  },
  data () {
    return {
      isEnter: false,
      openDoor: false,
      lottieNow: null, // 用來儲存lottie，方便接下來的調用
      lottieNowMobile: null
    }
  },
  methods: {
    /* eslint-disable */
    enter () {
      const vm = this
      vm.isEnter = true
      vm.lottieNow.play()
      window.setTimeout(() => {
        vm.openDoor = true
      }, 2200)
    },
    enterMobile () {
      const vm = this
      vm.isEnter = true
      vm.lottieNowMobile.play()
      window.setTimeout(() => {
        vm.openDoor = true
      }, 2200)
    }
  },
  mounted () {
    /* eslint-disable */
    var lottieKnock = bodymovin.loadAnimation ({
      container: document.getElementById('knock'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      path: './knock.json'
    })
    var lottieKnock2 = bodymovin.loadAnimation ({
      container: document.getElementById('knockMobile'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      path: './knock.json'
    })
    var lottieOpen = bodymovin.loadAnimation ({
      container: document.getElementById('open'),
      renderer: 'svg',
      loop: false,
      autoplay: false,
      path: './dooropen.json'
    })
    var lottieOpen2 = bodymovin.loadAnimation ({
      container: document.getElementById('openMobile'),
      renderer: 'svg',
      loop: false,
      autoplay: false,
      path: './dooropen.json'
    })
    this.lottieNow = lottieOpen
    this.lottieNowMobile = lottieOpen2
  }
}
</script>

