import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Policy from '../views/Policy.vue'
import Privacy from '../views/Privacy.vue'
import Reset from '../views/ResetPassword.vue'
import Complete from '../views/CompleteRegister.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    redirect: '/'
  },
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/policy',
    name: 'policy',
    component: Policy
  },
  {
    path: '/privacy',
    name: 'privacy',
    component: Privacy
  },
  {
    path: '/reset/:email/:token',
    name: 'reset',
    component: Reset
  },
  {
    path: '/completeregister/:email/:token',
    name: 'complete',
    component: Complete
  }
]

const router = new VueRouter({
  // mode: 'history',
  routes
})

export default router
